import quickBtn from '@/components/mbbo/quickBtn/quickBtn.vue';
import navigation from '@/components/mbbo/navigation/navigation.vue';
import rTitle from '@/components/common/RHX/rTitle/rTitle';
import skipMask from './skipMask';
import xing from '@/static/mbcImg/common/xinghao.png';
import look from '@/static/mbcImg/my/look.png';
import del from '@/static/mbcImg/my/del.png';
import right from '@/static/mbcImg/my/right.png';
import { mapMutations, mapGetters } from 'vuex';
export default {
	data() {
		return {
			xing: xing,
			remnane: 0,
			rem: 0,
			desc: '',
			des: '',
			look: look,
			hiden: 2,
			num: 0,
			nums: 9999999999,
			edit: '',
			del: del,
			list: [],
			id: '',
			editleiid:'',
			arr: [],
			editleis: '',
			right: right,
			iscategory: false,
			arrs: [],
			tips:[
				{
					show: false
				},
				{
					show: false
				}
			],
			skipMaskShow: false, //默认不展示
			shipPublish: false, // 是否跳过
			dateLists:null, // 链接上的参数
			publishOrUpdata: 0, // 发布还是更新
		};
	},
	components: {
		quickBtn,
		navigation,
		rTitle,
		skipMask
	},
	onLoad() {
	},
	created() {
		this.shareEachPage(); // 分享
		let urlParamsTemp = JSON.parse(uni.getStorageSync('urlParamsTemp')); // 读取缓存的用户信息
		this.dateLists = this.urlCrypto(urlParamsTemp, 1)[0];
		this.publishOrUpdata = this.urlCrypto(urlParamsTemp, 1)[1];
		// console.log(this.publishOrUpdata, '发布还是跟新')
		// console.log(this.publishOrUpdata, '---------------this.publishOrUpdata---------------');
		this.editleis = this.dateLists.instrucCategoryStr;
		this.list = this.dateLists.keyList;
		// console.log(this.dateLists, '----------------------------------------------------')
		this.Varieties();
		if (!uni.getStorageSync('templateFinishSkip')) {
			this.shipPublish = false;
		} else {
			if (uni.getStorageSync('templateFinishSkip') === true) {
				this.shipPublish = true;
			}
		}
		// console.log(this.dateLists, '----------------------------------------------------')
	},
	methods: {
		clickShowTitle(e) {
			this.tips[e].show = !this.tips[e].show
		},
		Varieties(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				// console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({
					// 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/instruc/getTypes', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
					},
					success: response => {
						uni.hideLoading();
						// console.log(response.data);
						if (response.data.ret === '200') {
							this.arrs=response.data.content;
						} else if (response.data.ret === '202') {
						 	uni.removeStorageSync('landRegist');
						 	uni.removeStorageSync('clickItems');
						 	this.$store.commit('setHome', 1);
						 	this.$store.commit('setLandRegist', 0);
							uni.showToast({
								title: '登录已过期，请重新登录',
								icon: 'none',
								duration: 1000
							});
							if (!uni.getStorageSync('landRegist')) {
								this.landRegistra(); // 判断登录状态
							}
						} else if (response.data.ret === '400') {
							uni.showToast({
								title: response.data.msg,
								icon: 'none',
								duration: 1000
							});
						} else {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络开小差了，请稍后重试',
								icon: 'none',
								duration: 1000
							});
						}
					},
					fail: error => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						// console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		hidens(item, e) {
			// console.log(item, e, '');
			this.dateLists.instrucCategory = item.id;
			this.editleis = item.typeName;
			this.iscategory = !this.iscategory
		},
		editpin() {
			this.iscategory = true;
			this.arrs.map((items,index)=>{
				if(this.editleis===items.typeName){
					this.nums = index
				}
			})
		},
		quxiao() {
			this.iscategory = false;
		},
		dele(e) {
			this.list.splice(e, 1);
			// console.log(this.list);
		},
		instrucTitle(e) {
			// console.log(e)
			this.dateLists.instrucTitle = e[1];
		},
		instrucIntro(e) {
			// console.log(e)
			this.dateLists.instrucIntro = e[1];
		},
		add() {
			this.hiden = '1';
			this.num = '1';
			// console.log(this.list);
		},
		sub() {
			if (this.edit === '') {
				// 失脚时没有数据
				this.hiden = 2;
			} else {
				// 失焦时存在数据
				this.hiden = 2;
				// console.log('创建新标签');
				if (this.list.length > 14) {
					this.edit = '';
					uni.showToast({
						title: '最多只能添加15个标签',
						icon: 'none',
						duration: 1000
					});
					return false;
				} else {
					this.list.push(this.edit);
					this.edit = '';
				}
			}
		},
		clickSkipBtn () { // 跳过
			// console.log('跳过');
			if (!uni.getStorageSync('templateFinishSkip')) {
				this.skipMaskShow = true;
			} else {
				// console.log('直接提交');
			}
		},
		tapSkipMask(e) {
			console.log(e, '操作shipMask');
			this.skipMaskShow = e[0];
			if (e[1] === 1) {
				this.shipPublish = true;
				this.editbao()
			}
		},
		editbao() {
			let params = {}; // 请求总数居时 参数为空
			if (!uni.getStorageSync('landRegist')) {
				this.landRegistra(); // 判断登录状态
			} else if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				// console.log(landRegistLG.user.id);
				params = this.dateLists;
				params.keyList = this.list;
				if (params.instrucTitle === '') {
					uni.showToast({
						title: '请填写说明书名称!',
						icon: 'none',
						duration: 1000
					});
					return
				};
				if (params.instrucIntro === '') {
					uni.showToast({
						title: '请填写说明书简介!',
						icon: 'none',
						duration: 1000
					});
					return
				};
				if (params.instrucCategory === '') {
					uni.showToast({
						title: '请选择所属品类!',
						icon: 'none',
						duration: 1000
					});
					return
				};
				// console.log(params.keyList, '-------params.keyList--------');
				let keyLists = [];
				if (params.keyList) {
					params.keyList.map((items, index)=>{
						if (items !== '') {
							keyLists.push(items);
						}
					});
				}
				params.keyList = keyLists;
				if (params.keyList.length < 1 && this.shipPublish === false && !uni.getStorageSync('templateFinishSkip')) {
					this.skipMaskShow = true;
					return
				};
				// 请求总数居时 参数为空
				uni.showLoading({
					// 展示loading
					title: '加载中'
				});
				if (this.publishOrUpdata === 0) {
					delete params.instrucCategoryStr;
					
					uni.request({
						url: this.api2 + '/rest-rp/instruc', //接口地址。
						data: params,
						method: 'POST',
						header: {
							Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
						},
						success: response => {
							if (response.data.ret === '200') {
								uni.hideLoading();
								// console.log(response.data.content);
								uni.navigateTo({
									url: '/modules/createBusiness/templateFinish/templateFinishShare?urlParams=' + this.urlCrypto(response.data.content, 0)
								});
							} else if (response.data.ret === '202') {
								uni.removeStorageSync('landRegist');
								uni.removeStorageSync('clickItems');
								this.$store.commit('setHome', 1);
								this.$store.commit('setLandRegist', 0);
								uni.showToast({
									title: '登录已过期，请重新登录',
									icon: 'none',
									duration: 1000
								});
								if (!uni.getStorageSync('landRegist')) {
									this.landRegistra(); // 判断登录状态
								}
							} else if (response.data.ret === '400') {
								uni.showToast({
									title: response.data.msg,
									icon: 'none',
									duration: 1000
								});
							} else {
								uni.hideLoading(); // 隐藏 loading
								uni.showToast({
									title: '网络开小差了，请稍后重试',
									icon: 'none',
									duration: 1000
								});
							}
						},
						fail: error => {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络繁忙，请稍后',
								icon: 'none',
								duration: 1000
							});
							// console.log(error, '网络繁忙，请稍后');
						}
					});
				} else if (this.publishOrUpdata === 1) {
					delete params.instrucCategoryStr;
					let keyLists = [];
					params.keyList.map((items, index)=>{
						if (items !== '') {
							keyLists.push(items);
						}
					});
					params.keyList = keyLists;
					uni.request({
						url: this.api2 + '/rest-rp/instruc/' + params.context.instrucId, //接口地址。
						data: params,
						method: 'POST',
						header: {
							Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
						},
						success: response => {
							if (response.data.ret === '200') {
								uni.hideLoading();
								// console.log(response.data.content);
								uni.navigateTo({
									url: '/modules/createBusiness/templateFinish/templateFinishShare?urlParams=' + this.urlCrypto(response.data.content, 0)
								});
							}  else if (response.data.ret === '202') {
								uni.hideLoading(); // 隐藏 loading
								uni.removeStorageSync('landRegist');
								uni.removeStorageSync('clickItems');
								this.$store.commit('setHome', 1);
								this.$store.commit('setLandRegist', 0);
								uni.showToast({
									title: '登录已过期，请重新登录',
									icon: 'none',
									duration: 1000
								});
								if (!uni.getStorageSync('landRegist')) {
									this.landRegistra(); // 判断登录状态
								}
							} else if (response.data.ret === '400') {
								uni.hideLoading(); // 隐藏 loading
								uni.showToast({
									title: response.data.msg,
									icon: 'none',
									duration: 1000
								});
							} else {
								uni.hideLoading(); // 隐藏 loading
								uni.showToast({
									title: '网络开小差了，请稍后重试',
									icon: 'none',
									duration: 1000
								});
							}
						},
						fail: error => {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络繁忙，请稍后',
								icon: 'none',
								duration: 1000
							});
							// console.log(error, '网络繁忙，请稍后');
						}
					});
				}
				
			}
		},
		
	},
	computed: {
		i18n() {
			return this.$t('Howtoshow');
		},
		...mapGetters(['GET_NEWS', 'GET_HOME', 'QUICKNAVCO'])
	}
};
